                <div class="lang_names">
                  {{lang_name}}
                  <b-icon icon="chevron-down" aria-hidden="true"></b-icon>
                </div>
              </template>
              <div class="lang_btn">
                <button class="mt-1 ms-1 lang " :class="{'active':isActive=='ru'}" @click="myFunks(isActive='ru')">Ru</button>
              </div>
              <div class="lang_btn">
                <button class="mt-1 ms-1 lang" :class="{'active':isActive=='uz-cyrl'}" @click="myFunks(isActive='uz-cyrl')">Uz</button>
              </div >
              <div class="lang_btn">
                <button class="mt-1 ms-1 lang" :class="{'active':isActive=='uz-latn'}" @click="myFunks(isActive='uz-latn')">Uz-latn</button>
              </div>
              <div class="lang_btn">
                <button class="mt-1 ms-1 lang" :class="{'active':isActive=='en'}" @click="myFunks(isActive='en')">En</button>
              </div>
            </b-dropdown>
