</template>
<script>
export default {
  data(){
    return{
      isActive:localStorage.getItem('lang'),
      lang_name:'Russian',
      IsActiveSwip:true
    }
  },
  mounted() {
    if(localStorage.getItem('lang')=='ru'){
      this.lang_name='Russian'
    }
    if(localStorage.getItem('lang')=='en'){
      this.lang_name='English'
    }
    if(localStorage.getItem('lang')=='uz-latn'){
      this.lang_name='Uz latin'
    }
    if(localStorage.getItem('lang')=='uz-cyrl'){
      this.lang_name='Uz cyrl'
    }
  },
  methods:{
    logout(){
      localStorage.clear()
      this.$router.push('/login')
    },
    myFunks(item){
      console.log(item)
      localStorage.setItem('lang',item)
      this.$router.go()
    }
  }

}
</script>
