<template>
  <div>
    <Navbar></Navbar>
    <div class=" main_card">
      <b-card no-body>
        <div class="row">
          <div class="col-md-9">
            <b-tabs card>
              <b-tab title="Rus" active>
                <b-card-text>
                  <form @submit.prevent="post_ru()">
                    <div class="row">
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">title</label>
                          <input class="form-control" id="first_namee" v-model="title" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">bank</label>
                          <input class="form-control" id="first_namee" v-model="bank" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">address</label>
                          <input class="form-control" id="first_namee" v-model="address" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">billing_address</label>
                          <input class="form-control" id="first_namee" v-model="billing_address" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">mfi</label>
                          <input class="form-control" id="first_namee" v-model="mfi" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">inn</label>
                          <input class="form-control" id="first_namee" v-model="inn" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">itn</label>
                          <input class="form-control" id="first_namee" v-model="itn" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">oked</label>
                          <input class="form-control" id="first_namee" v-model="oked" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">phone_number</label>
                          <input class="form-control" id="first_namee" v-model="phone_number" type="text"  >
                        </div>
                      </div>
                    </div>
                    <div class="mt-3">
                      <button class="btn btn-gray-800 mt-2 animate-up-2" type="submit">Save all</button>
                    </div>
                  </form>
                </b-card-text>
              </b-tab>
              <b-tab title="Uzbek lotin" >

                <b-card-text>
                  <form @submit.prevent="put_uzl()">

                    <div class="row">
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">title</label>
                          <input class="form-control" id="first_namee" v-model="title" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">bank</label>
                          <input class="form-control" id="first_namee" v-model="bank" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">address</label>
                          <input class="form-control" id="first_namee" v-model="address" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">billing_address</label>
                          <input class="form-control" id="first_namee" v-model="billing_address" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">mfi</label>
                          <input class="form-control" id="first_namee" v-model="mfi" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">inn</label>
                          <input class="form-control" id="first_namee" v-model="inn" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">mfi</label>
                          <input class="form-control" id="first_namee" v-model="mfi" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">itn</label>
                          <input class="form-control" id="first_namee" v-model="itn" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">oked</label>
                          <input class="form-control" id="first_namee" v-model="oked" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">phone_number</label>
                          <input class="form-control" id="first_namee" v-model="phone_number" type="text"  >
                        </div>
                      </div>
                    </div>
                    <div class="mt-3">
                      <button class="btn btn-gray-800 mt-2 animate-up-2" type="submit">Save all</button>
                    </div>
                  </form>
                </b-card-text>
              </b-tab>
            </b-tabs>
          </div>

          <div class="col-md-3 media">
            <div>
              <input type="file" class="d-none" id="cr_image"
                     multiple
                     @change="trySubmitFile"
                     accept="*"
                     name="datafiles"
                     ref="datafiles"
              >
              <div>
                <div class="main_image" v-for="(item,index) in imageUrl " :key="index">
                  <div>{{ image_name[index] }}</div>
                  <img :src="item" width="97%" height="80px" style="object-fit: cover;margin-bottom: 40px" alt="">
                </div>
                <label for="cr_image" class="btn btn-primary file_label">images</label>
              </div>

            </div>
          </div>
        </div>
      </b-card>
    </div>
  </div>
</template>
<script>
import Navbar from "../../components/Navbar";
export default {
  components: {
    Navbar
  },
  data(){
    return{
      about1:'',
      about2:'',
      about3:'',
      about4:'',
      id:'',
      title:'',
      selected:[],
      document_number:'',
      published_date:'',
      selectitem:'',
      instagram_link:'',
      twitter_link:'',
      facebook_link:'',
      leader:'',
      myImages:[],
      imageUrl:[],
      files_name:[],
      image_name:['File'],
      myImages1:[],
      imageUrl1:[],
      files_name1:[],
      about:'',
      first_task:'',
      second_task:'',
      third_task:'',
      fourth_task:'',
      first_title_task:'',
      second_title_task:'',
      third_title_task:'',
      fourth_title_task:'',
      link:'',
      status:'',


      title1:'',
      leader1:'',
      title2:'',
      leader2:'',


      phone_number:'',
      oked:'',
      itn:'',
      inn:'',
      mfi:'',
      billing_address:'',
      address:'',
      bank:'',

    }
  },
  mounted() {
    this.$http.get('/api/management/'+this.$route.query.id+'/',
        {
          headers: {
            'Accept-Language':'ru'
          }
        })

        .then(res=>{
          this.title1=res.data.title,
              this.leader1=res.data.leader
          this.facebook_link=res.data.facebook_link,
              this.instagram_link=res.data.instagram_link,
              this.twitter_link=res.data.twitter_link
        })


    this.$http.get('/api/management/'+this.$route.query.id+'/',
        {
          headers: {
            'Accept-Language':'uz-latn'
          }
        })

        .then(res=>{
          this.title2=res.data.title,
              this.leader2=res.data.leader

        })

  },
  methods:{

    trySubmitFile(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    trySubmitFile1(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name1.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages1.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl1.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    post_ru(){

      const form = new FormData();

      form.append("title", this.title);
      form.append("bank", this.bank);
      form.append("address", this.address);
      form.append("billing_address", this.billing_address);
      form.append("mfi", this.mfi);
      form.append("itn", this.itn);
      form.append("inn", this.inn);
      form.append("itn", this.itn);
      form.append("itn", this.itn);
      form.append("oked", this.oked);
      form.append("phone_number", this.phone_number);
      form.append("file", this.myImages[0]);
      this.$http.post('/api/requisite/',
          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'ru'
            }
          })

          .then(res=>{
            this.$toasted.show("Create data success",{
              duration: 1000,
              'type':'info'

            })
            this.id=res.data.id
            this.title='',
                this.about='',
                this.document_number='',
                this.published_date='',
                this.selectitem=''
          })
          .catch(()=> {
            this.$toasted.show("Errors", {
              duration: 1000,
              'type': 'danger'
            })
          })
    },
    put_uzl(){

      const form = new FormData();

      form.append("title", this.title);
      form.append("bank", this.bank);
      form.append("address", this.address);
      form.append("billing_address", this.billing_address);
      form.append("mfi", this.mfi);
      form.append("itn", this.itn);
      form.append("inn", this.inn);
      form.append("itn", this.itn);
      form.append("itn", this.itn);
      form.append("oked", this.oked);
      form.append("phone_number", this.phone_number);
      form.append("file", this.myImages[0]);
      this.$http.put('/api/requisite/'+this.$route.query.id+'/',


          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'uz-latn'
            }
          })

          .then(()=>{
            this.$toasted.show("Create data success",{
              duration: 1000,
              'type':'info'

            })

            this.title=''
            this.document_number='',
                this.published_date='',
                this.selectitem='',
                this.about=''
          })
          .catch(()=> {
            this.$toasted.show("Errors", {
              duration: 1000,
              'type': 'danger'
            })
          })
    },
    put_uzk(){

      const form = new FormData();

      for(let i=0;i<this.myImages1.length;i++){
        form.append(`images[${i}]image`,  this.myImages1[i]);
      }
      form.append("about", this.about3);
      form.append("main_image", this.myImages[0]);
      this.$http.post('/api/about/'+this.id+'/',
          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'uz-cyrl'
            }
          })

          .then(()=>{
            this.$toasted.show("Create data success",{
              duration: 1000,
              'type':'info'

            })

            this.title=''
            this.about='',
                this.document_number='',
                this.published_date='',
                this.selectitem=''
          })
          .catch(()=> {
            this.$toasted.show("Errors", {
              duration: 1000,
              'type': 'danger'
            })
          })
    },
    put_en(){

      const form = new FormData();

      for(let i=0;i<this.myImages1.length;i++){
        form.append(`images[${i}]image`,  this.myImages1[i]);
      }
      form.append("about", this.about4);
      form.append("main_image", this.myImages[0]);
      this.$http.post('/api/about/'+this.id+'/',
          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'en'
            }
          })

          .then(()=>{
            this.$toasted.show("Create data success",{
              duration: 1000,
              'type':'info'

            })

            this.about='',
                this.title=''
            this.document_number='',
                this.published_date='',
                this.selectitem=''
          })
          .catch(()=> {
            this.$toasted.show("Errors", {
              duration: 1000,
              'type': 'danger'
            })
          })
    }
  }
}
</script>
