                          <input class="form-control" id="first_namee" v-model="file_title" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">file_description</label>
                          <input class="form-control" id="first_namee" v-model="file_description" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">Link</label>
                          <input class="form-control" id="first_namee" v-model="link" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">published_date</label>
                          <input class="form-control" id="first_namee" v-model="published_date" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">content</label>

                          <quill-editor
                              :required="true"
                              ref="myQuillEditorOrg"
                              v-model="about1"
                          />
                        </div>
                      </div>
                    </div>
                    <div class="mt-3">
                      <button class="btn btn-gray-800 mt-2 animate-up-2" type="submit">Save all</button>
                    </div>
                  </form>
                </b-card-text>
              </b-tab>
              <b-tab title="Uzbek lotin" >

                <b-card-text>
                  <form @submit.prevent="put_uzl()">

                    <div class="row">
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">Title</label>
                          <input class="form-control" id="first_namee" v-model="title" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">file_title</label>
                          <input class="form-control" id="first_namee" v-model="file_title" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">file_description</label>
                          <input class="form-control" id="first_namee" v-model="file_description" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">Link</label>
                          <input class="form-control" id="first_namee" v-model="link" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">published_date</label>
                          <input class="form-control" id="first_namee" v-model="published_date" type="text"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">content</label>

                          <quill-editor
                              :required="true"
                              ref="myQuillEditorOrg"
                              v-model="about1"
                          />
                        </div>
                      </div>
                    </div>
                    <div class="mt-3">
                      <button class="btn btn-gray-800 mt-2 animate-up-2" type="submit">Save all</button>
                    </div>
                  </form>
                </b-card-text>
              </b-tab>
            </b-tabs>
          </div>

          <!--          <div class="col-md-3 media">-->
          <!--            <div>-->
          <!--              <input type="file" class="d-none" id="cr_image"-->
          <!--                     multiple-->
          <!--                     @change="trySubmitFile"-->
          <!--                     accept="image/*"-->
          <!--                     name="datafiles"-->
          <!--                     ref="datafiles"-->
          <!--              >-->
          <!--              <div>-->
          <!--                <div class="main_image" v-for="(item,index) in imageUrl " :key="index">-->
          <!--                  <div>{{ image_name[index] }}</div>-->
          <!--                  <img :src="item" width="97%" height="80px" style="object-fit: cover;margin-bottom: 40px" alt="">-->
          <!--                </div>-->
          <!--                <label for="cr_image" class="btn btn-primary file_label">images</label>-->
          <!--              </div>-->

          <!--            </div>-->
          <!--          </div>-->
        </div>
      </b-card>
    </div>
  </div>
</template>
<script>
import Navbar from "../../components/Navbar";
export default {
  components: {
    Navbar
  },
  data(){
    return{
      about1:'',
      about2:'',
      about3:'',
      about4:'',
      id:'',
      title:'',
      selected:[],
      document_number:'',
      published_date:'',
      selectitem:'',

      myImages:[],
      imageUrl:[],
      files_name:[],
      image_name:['planshet_image','maiin_image','icon1','icon2','icon3','icon4'],
      myImages1:[],
      imageUrl1:[],
      files_name1:[],
      about:'',
      first_task:'',
      second_task:'',
      third_task:'',
      fourth_task:'',
      first_title_task:'',
      second_title_task:'',
      third_title_task:'',
      fourth_title_task:'',
      link:'',
      status:'',
      content:'',
      file_description:'',
      file_title:''


    }
  },
  mounted() {
    this.$http.get('/api/docs')
        .then(res=>{
          this.selected=res.data
        })
  },
  methods:{

    trySubmitFile(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    trySubmitFile1(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name1.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages1.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl1.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    post_ru(){

      const form = new FormData();

      form.append("title", this.title);
      form.append("content", this.about1);
      form.append("file_description", this.file_description);

      form.append("file_title", this.file_title);
      form.append("link", this.link);
      this.$http.post('/api/reglament/',
          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'ru'
            }
          })

          .then(res=>{
            this.$toasted.show("Create data success",{
              duration: 1000,
              'type':'info'

            })
            this.id=res.data.id
            this.title='',
                this.about='',
                this.document_number='',
                this.published_date='',
                this.selectitem=''
          })
          .catch(()=> {
            this.$toasted.show("Errors", {
              duration: 1000,
              'type': 'danger'
            })
          })
    },
    put_uzl(){


      const form = new FormData();

      form.append("title", this.title);
      form.append("content", this.about1);
      form.append("file_description", this.file_description);

      form.append("file_title", this.file_title);
      form.append("link", this.link);
      form.append("published_date", this.published_date);
      this.$http.put('/api/reglament/'+this.id+'/',

          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'uz-latn'
            }
          })

          .then(()=>{
            this.$toasted.show("Create data success",{
              duration: 1000,
              'type':'info'

            })

            this.title=''
            this.document_number='',
                this.published_date='',
                this.selectitem='',
                this.about=''
          })
          .catch(()=> {
            this.$toasted.show("Errors", {
              duration: 1000,
              'type': 'danger'
            })
          })
    },
    put_uzk(){

      const form = new FormData();

      for(let i=0;i<this.myImages1.length;i++){
        form.append(`images[${i}]image`,  this.myImages1[i]);
      }
      form.append("about", this.about3);
      form.append("main_image", this.myImages[0]);
      this.$http.post('/api/about/'+this.id+'/',
          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'uz-cyrl'
            }
          })

          .then(()=>{
            this.$toasted.show("Create data success",{
              duration: 1000,
              'type':'info'

            })

            this.title=''
            this.about='',
                this.document_number='',
                this.published_date='',
                this.selectitem=''
          })
          .catch(()=> {
            this.$toasted.show("Errors", {
              duration: 1000,
              'type': 'danger'
            })
          })
    },
    put_en(){

      const form = new FormData();

      for(let i=0;i<this.myImages1.length;i++){
        form.append(`images[${i}]image`,  this.myImages1[i]);
      }
      form.append("about", this.about4);
      form.append("main_image", this.myImages[0]);
      this.$http.post('/api/about/'+this.id+'/',
          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'en'
            }
          })

          .then(()=>{
            this.$toasted.show("Create data success",{
              duration: 1000,
              'type':'info'

            })

            this.about='',
                this.title=''
            this.document_number='',
                this.published_date='',
                this.selectitem=''
          })
          .catch(()=> {
            this.$toasted.show("Errors", {
              duration: 1000,
              'type': 'danger'
            })
          })
    }
  }
}
</script>
