              <b-tab title="Rus" active>
                <b-card-text>
                  <form @submit.prevent="post_ru()">
                    <div class="row">
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">Title</label>
                          <input class="form-control" id="first_namee" v-model="title1" type="text"  >
                        </div>
                      </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">first_task</label>
                        <input class="form-control" id="first_namee" v-model="first_task1" type="text"  >
                      </div>
                    </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">second_task</label>
                        <input class="form-control" id="first_namee" v-model="second_task1" type="text"  >
                      </div>
                    </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">third_task</label>
                        <input class="form-control" id="first_namee" v-model="third_task1" type="text"  >
                      </div>
                    </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">fourth_task</label>
                        <input class="form-control" id="first_namee" v-model="fourth_task1" type="text"  >
                      </div>
                    </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">first_title_task</label>
                        <input class="form-control" id="first_namee" v-model="first_title_task1" type="text"  >
                      </div>
                    </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">second_title_task</label>
                          <input class="form-control" id="first_namee" v-model="second_title_task1" type="text"  >
                        </div>
                      </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">third_title_task</label>
                        <input class="form-control" id="first_namee" v-model="third_title_task1" type="text"  >
                      </div>
                    </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">fourth_title_task</label>
                        <input class="form-control" id="first_namee" v-model="fourth_title_task1" type="text"  >
                      </div>
                    </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">link</label>
                        <input class="form-control" id="first_namee" v-model="link" type="text"  >
                      </div>
                    </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">0</label>
                        <input  id="first_namee" v-model="status" name="check" value="0" type="radio"  >
                      </div>
                    </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">1</label>
                          <input  id="first_namee" v-model="status" name="check" value="0" type="radio"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">about</label>

                          <quill-editor
                              :required="true"
                              ref="myQuillEditorOrg"
                              v-model="about1"
                          />
                        </div>
                      </div>
                    </div>
                    <div class="mt-3">
                      <button class="btn btn-gray-800 mt-2 animate-up-2" type="submit">Save all</button>
                    </div>
                  </form>
                </b-card-text>
              </b-tab>
              <b-tab title="Uzbek lotin" >

                <b-card-text>
                  <form @submit.prevent="put_uzl()">

                    <div class="row">
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">Title</label>
                          <input class="form-control" id="first_namee" v-model="title2" type="text"  >
                        </div>
                      </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">first_task</label>
                        <input class="form-control" id="first_namee" v-model="first_task2" type="text"  >
                      </div>
                    </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">second_task</label>
                        <input class="form-control" id="first_namee" v-model="second_task2" type="text"  >
                      </div>
                    </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">third_task</label>
                        <input class="form-control" id="first_namee" v-model="third_task2" type="text"  >
                      </div>
                    </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">fourth_task</label>
                        <input class="form-control" id="first_namee" v-model="fourth_task2" type="text"  >
                      </div>
                    </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">first_title_task</label>
                        <input class="form-control" id="first_namee" v-model="first_title_task2" type="text"  >
                      </div>
                    </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">second_title_task</label>
                          <input class="form-control" id="first_namee" v-model="second_title_task2" type="text"  >
                        </div>
                      </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">third_title_task</label>
                        <input class="form-control" id="first_namee" v-model="third_title_task2" type="text"  >
                      </div>
                    </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">fourth_title_task</label>
                        <input class="form-control" id="first_namee" v-model="fourth_title_task2" type="text"  >
                      </div>
                    </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">link</label>
                        <input class="form-control" id="first_namee" v-model="link" type="text"  >
                      </div>
                    </div> <div class="col-md-12  mb-3">
                      <div>
                        <label for="first_name">0</label>
                        <input  id="first_namee" v-model="status" name="check" value="0" type="radio"  >
                      </div>
                    </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">1</label>
                          <input  id="first_namee" v-model="status" name="check" value="0" type="radio"  >
                        </div>
                      </div>
                      <div class="col-md-12  mb-3">
                        <div>
                          <label for="first_name">about</label>

                          <quill-editor
                              :required="true"
                              ref="myQuillEditorOrg"
                              v-model="about2"
                          />
                        </div>
                      </div>
                    </div>
                    <div class="mt-3">
                      <button class="btn btn-gray-800 mt-2 animate-up-2" type="submit">Save all</button>
                    </div>
                  </form>
                </b-card-text>
              </b-tab>
            </b-tabs>
          </div>

          <div class="col-md-3 media">
            <div>
              <input type="file" class="d-none" id="cr_image"
                     multiple
                     @change="trySubmitFile"
                     accept="image/*"
                     name="datafiles"
                     ref="datafiles"
              >
              <div>
                <div class="main_image" v-for="(item,index) in imageUrl " :key="index">
                  <div>{{ image_name[index] }}</div>
                  <img :src="item" width="97%" height="80px" style="object-fit: cover;margin-bottom: 40px" alt="">
                </div>
                <label for="cr_image" class="btn btn-primary file_label">images</label>
              </div>

            </div>
          </div>
        </div>
      </b-card>
    </div>
  </div>
</template>
<script>
import Navbar from "../../components/Navbar";
export default {
  components: {
    Navbar
  },
  data(){
    return{
      about3:'',
      about4:'',
      id:'',
      title:'',
      selected:[],
      document_number:'',
      published_date:'',
      selectitem:'',

      myImages:[],
      imageUrl:[],
      files_name:[],
      image_name:['planshet_image','maiin_image','icon1','icon2','icon3','icon4'],
      myImages1:[],
      imageUrl1:[],
      files_name1:[],
      about1:'',
      first_task1:'',
      second_task1:'',
      third_task1:'',
      fourth_task1:'',
      first_title_task1:'',
      second_title_task1:'',
      third_title_task1:'',
      fourth_title_task1:'',


      about2:'',
      first_task2:'',
      second_task2:'',
      third_task2:'',
      fourth_task2:'',
      first_title_task2:'',
      second_title_task2:'',
      third_title_task2:'',
      fourth_title_task2:'',
      link:'',
      status:'',


    }
  },

  mounted() {
    this.$http.get('/api/direction/'+this.$route.query.id+'/',
        {
          headers: {
            'Accept-Language':'ru'
          }
        })

        .then(res=>{
          this.title1=res.data.title,
          this.about1=res.data.about,
          this.first_task1=res.data.first_task,
          this.second_task1=res.data.second_task,
          this.third_task1=res.data.third_task,
          this.fourth_task1=res.data.fourth_task,
          this.first_title_task1=res.data.first_title_task,
          this.second_title_task1=res.data.second_title_task,
          this.third_title_task1=res.data.third_title_task,
          this.fourth_title_task1=res.data.fourth_title_task

        })


    this.$http.get('/api/direction/'+this.$route.query.id+'/',
        {
          headers: {
            'Accept-Language':'uz-latn'
          }
        })

        .then(res=>{

          this.title2=res.data.title,
          this.about2=res.data.about,
          this.first_task2=res.data.first_task,
          this.second_task2=res.data.second_task,
          this.third_task2=res.data.third_task,
          this.fourth_task2=res.data.fourth_task,
          this.first_title_task2=res.data.first_title_task,
          this.second_title_task2=res.data.second_title_task,
          this.third_title_task2=res.data.third_title_task,
          this.fourth_title_task2=res.data.fourth_title_task
        })

    this.id=this.$route.query.id
  },
  methods:{

    trySubmitFile(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    trySubmitFile1(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name1.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages1.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl1.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    post_ru(){
      this.icon1=this.myImages[2]
      this.icon2=this.myImages[3]
      if(this.myImages[4]){
        this.icon3=this.myImages[4]
      }
      else {
        this.icon3=""
      }
      if(this.myImages[5]){
        this.icon4=this.myImages[5]
      }
      else {
        this.icon4=""
      }
      const form = new FormData();
      form.append("title", this.title);
      form.append("about", this.about);
      form.append("first_task", this.first_task);
      form.append("second_task", this.second_task);
      form.append("third_task", this.third_task);
      form.append("fourth_task", this.fourth_task);
      form.append("first_title_task", this.first_title_task);
      form.append("second_title_task", this.second_title_task);
      form.append("third_title_task", this.third_title_task);
      form.append("fourth_title_task", this.fourth_title_task);
      form.append("link", this.link);
      form.append("first_icon", this.icon1);
      form.append("second_icon",  this.icon2);
      form.append("third_icon",  this.icon3);
      form.append("fourth_icon", this.icon4);
      form.append("planshet_image", this.myImages[0]);
      form.append("main_image", this.myImages[1]);
      this.$http.put('/api/direction/'+this.$route.query.id+'/',
          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'ru'
            }
          })

          .then(res=>{
            this.$toasted.show("Create data success",{
              duration: 1000,
              'type':'info'

            })
            this.id=res.data.id
            this.title='',
                this.about='',
                this.document_number='',
                this.published_date='',
                this.selectitem=''
          })
          .catch(()=> {
            this.$toasted.show("Errors", {
              duration: 1000,
              'type': 'danger'
            })
          })
    },
    put_uzl(){

      this.icon1=this.myImages[2]
      this.icon2=this.myImages[3]

      if(this.myImages[4]){
        this.icon3=this.myImages[4]
      }
      else {
        this.icon3=""
      }
      if(this.myImages[5]){
        this.icon4=this.myImages[5]
      }
      else {
        this.icon4=""
      }
      const form = new FormData();

      form.append("title", this.title2);
      form.append("about", this.about2);
      form.append("first_task", this.first_task2);
      form.append("second_task", this.second_task2);
      form.append("third_task", this.third_task2);
      form.append("fourth_task", this.fourth_task2);
      form.append("first_title_task", this.first_title_task2);
      form.append("second_title_task", this.second_title_task2);
      form.append("third_title_task", this.third_title_task2);
      form.append("fourth_title_task", this.fourth_title_task2);
      form.append("link", this.link);
      form.append("first_icon", this.icon1);
      form.append("second_icon",  this.icon2);
      form.append("third_icon",  this.icon3);
      form.append("fourth_icon", this.icon4);
      form.append("planshet_image", this.myImages[0]);
      form.append("main_image", this.myImages[1]);
      this.$http.put('/api/direction/'+this.$route.query.id+'/',
          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'uz-latn'
            }
          })

          .then(()=>{
            this.$toasted.show("Create data success",{
              duration: 1000,
              'type':'info'

            })

            this.title=''
            this.document_number='',
                this.published_date='',
                this.selectitem='',
                this.about=''
          })
          .catch(()=> {
            this.$toasted.show("Errors", {
              duration: 1000,
              'type': 'danger'
            })
          })
    },
    put_uzk(){

      const form = new FormData();

      for(let i=0;i<this.myImages1.length;i++){
        form.append(`images[${i}]image`,  this.myImages1[i]);
      }
      form.append("about", this.about3);
      form.append("main_image", this.myImages[0]);
      this.$http.post('/api/about/'+this.id+'/',
          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'uz-cyrl'
            }
          })

          .then(()=>{
            this.$toasted.show("Create data success",{
              duration: 1000,
              'type':'info'

            })

            this.title=''
            this.about='',
                this.document_number='',
                this.published_date='',
                this.selectitem=''
          })
          .catch(()=> {
            this.$toasted.show("Errors", {
              duration: 1000,
              'type': 'danger'
            })
          })
    },
    put_en(){

      const form = new FormData();

      for(let i=0;i<this.myImages1.length;i++){
        form.append(`images[${i}]image`,  this.myImages1[i]);
      }
      form.append("about", this.about4);
      form.append("main_image", this.myImages[0]);
      this.$http.post('/api/about/'+this.id+'/',
          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'en'
            }
          })

          .then(()=>{
            this.$toasted.show("Create data success",{
              duration: 1000,
              'type':'info'

            })

            this.about='',
                this.title=''
            this.document_number='',
                this.published_date='',
                this.selectitem=''
          })
          .catch(()=> {
            this.$toasted.show("Errors", {
              duration: 1000,
              'type': 'danger'
            })
          })
    }
  }
}
</script>
<style>
.main_card{
  margin-top: 50px;
}
.form-inline{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
}
.custom-select{
  height: 30px;
  margin: 20px;
}
.media{
  margin-top: 280px;
}
.file_label{
  width: 200px;
}
</style>
