  </div>
</template>
<script>

import Navbar from "../../components/Navbar";
export default {
  components: {
    Navbar,
  },
  data(){
    return{
      id:'',
      selected:[],
      selectitem:'',
      leader:'',
      task:'',
      previous_work:'',
      education:'',
      department:'',
      birthdate:'',
      phone_number:'',
      email:'',
      myImages:[],
      imageUrl:[],
      files_name:[],
      main_about:'',
      order:'',

      title1:'',
      reception1:'',
      address1:'',
      description1:'',


      title2:'',
      reception2:'',
      address2:'',
      description2:'',


      title3:'',
      reception3:'',
      address3:'',
      description3:'',


      title4:'',
      reception4:'',
      address4:'',
      description4:'',
      latitude:'',
      longitude:'',
      address:''
    }
  },
  methods:{
    trySubmitFile(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    post_ru(){
      const form = new FormData();
      form.append("order", this.order);
      form.append("main_about", this.main_about);
      form.append("task", this.task);
      form.append("address", this.address);
      form.append("email", this.email);
      form.append("phone_number", this.phone_number);
      form.append("longitude",this.longitude);
      form.append("latitude",this.latitude);
      this.$http.put('/api/about/'+this.$route.query.id+'/',
          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'ru'
            }
          })

          .then(res=>{
            this.$toasted.show("Create data success",{
              duration: 1000,
              'type':'info'

            })
            this.id=res.data.id
            this.title=''
            this.document_number=''
            this.published_date=''
            this.leader=''
            this.reception=''
            this.task=''
            this.previous_work=''
            this.education=''
            this.department=''
            this.birthdate=''
            this.phone_number=''
            this.email=''
          })
          .catch(()=> {
            this.$toasted.show("Errors", {
              duration: 1000,
              'type': 'danger'
            })
          })
    },
    put_uzl(){

      const form = new FormData();
      form.append("main_image", this.myImages[0]);
      form.append("images[0]image", this.myImages[0]);
      form.append("address", this.address2);
      form.append("title", this.title2);
      form.append("reception", this.reception2);
      form.append("description", this.description2);
      form.append("phone_number",this.phone_number);
      this.$http.put('/api/about/'+this.id+'/',
          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'uz-latn'
            }
          })

          .then(()=>{
            this.$toasted.show("Create data success",{
              duration: 1000,
              'type':'info'

            })

            this.title=''
            this.document_number=''
            this.published_date=''
            this.leader=''
            this.reception=''
            this.task=''
            this.previous_work=''
            this.education=''
            this.department=''
            this.birthdate=''
            this.phone_number=''
            this.email=''
          })
          .catch(()=> {
            this.$toasted.show("Errors", {
              duration: 1000,
              'type': 'danger'
            })
          })
    },
    put_uzk(){

      const form = new FormData();
      form.append("main_image", this.myImages[0]);
      form.append("images[0]image", this.myImages[0]);
      form.append("address", this.address3);
      form.append("title", this.title3);
      form.append("reception", this.reception3);
      form.append("description", this.description3);
      form.append("phone_number",this.phone_number);
      this.$http.put('/api/museum/'+this.id+'/',

          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'uz-cyrl'
            }
          })

          .then(()=>{
            this.$toasted.show("Create data success",{
              duration: 1000,
              'type':'info'

            })
            this.title=''
            this.document_number=''
            this.published_date=''
            this.leader=''
            this.reception=''
            this.task=''
            this.previous_work=''
            this.education=''
            this.department=''
            this.birthdate=''
            this.phone_number=''
            this.email=''
          })
          .catch(()=> {
            this.$toasted.show("Errors", {
              duration: 1000,
              'type': 'danger'
            })
          })
    },
    put_en(){

      const form = new FormData();
      form.append("main_image", this.myImages[0]);
      form.append("images[0]image", this.myImages[0]);
      form.append("address", this.address4);
      form.append("title", this.title4);
      form.append("reception", this.reception4);
      form.append("description", this.description4);
      form.append("phone_number",this.phone_number);
      this.$http.put('/api/museum/'+this.id+'/',

          form,
          {
            headers: {
              'Authorization': 'Token ' + localStorage.getItem('m_token'),
              'Accept-Language':'en'
            }
          })

          .then(()=>{
            this.$toasted.show("Create data success",{
              duration: 1000,
              'type':'info'

            })

            this.title=''
            this.document_number=''
            this.published_date=''
            this.leader=''
            this.reception=''
            this.task=''
            this.previous_work=''
            this.education=''
            this.department=''
            this.birthdate=''
            this.phone_number=''
            this.email=''
          })
          .catch(()=> {
            this.$toasted.show("Errors", {
              duration: 1000,
              'type': 'danger'
            })
          })
    }
  },

  computed: {
    editor() {
      return this.$refs.myQuillEditorOrg.quill;
    },
  },
}
</script>
